import classNames from 'classnames';
import React from 'react';
import { Form, Field } from 'react-final-form';

const isDanger = meta => meta.error && meta.touched;

const renderInput = (input, meta, placeholder, submitting) => (
  <div className="field">
    <div className="control">
      <input
        {...input}
        className={classNames({ input: true, 'is-danger': isDanger(meta) })}
        disabled={submitting ? true : false}
        placeholder={placeholder}
        type="text"
      />
    </div>
    {isDanger(meta) && <p className="help is-danger">{meta.error}</p>}
  </div>
);

const ContactForm = ({ copy, notification, onClick, onSubmit }) => {
  return [
    notification && (
      <div
        className={classNames({ notification: true, 'is-success': true })}
        key="notification"
      >
        <button className="delete" onClick={onClick} />
        {copy['thanks-for-filling-out-contact-form']}
      </div>
    ),
    <Form
      initialValues={{ email: '', message: '', name: '', subject: '' }}
      key="form"
      onSubmit={onSubmit}
      render={({ form, handleSubmit, submitting, values }) => (
        <form
          onSubmit={event => {
            event.preventDefault();
            handleSubmit(event).then(() => {
              form.reset();
            });
          }}
        >
          <Field name="name">
            {({ input, meta }) =>
              renderInput(input, meta, copy['name'], submitting)
            }
          </Field>
          <Field name="email">
            {({ input, meta }) =>
              renderInput(input, meta, copy['email'], submitting)
            }
          </Field>
          <Field name="subject">
            {({ input, meta }) =>
              renderInput(input, meta, copy['subject'], submitting)
            }
          </Field>
          <Field name="message">
            {({ input, meta }) => (
              <div className="field">
                <div className="control">
                  <textarea
                    {...input}
                    className={classNames({
                      textarea: true,
                      'is-danger': isDanger(meta)
                    })}
                    disabled={submitting ? true : false}
                    placeholder={copy['message']}
                  />
                </div>
                {isDanger(meta) && (
                  <p className="help is-danger">{meta.error}</p>
                )}
              </div>
            )}
          </Field>
          <div className="field">
            <div className="control">
              <button
                className={classNames({
                  button: true,
                  'is-link': true,
                  'is-loading': submitting
                })}
                disabled={submitting}
              >
                {copy['send-message']}
              </button>
            </div>
          </div>
        </form>
      )}
      validate={values => {
        const errors = {};
        if (!values.email) {
          errors.email = copy['required'];
        }
        if (!values.message) {
          errors.message = copy['required'];
        }
        if (!values.name) {
          errors.name = copy['required'];
        }
        if (!values.subject) {
          errors.subject = copy['required'];
        }
        return errors;
      }}
    />
  ];
};

export default ContactForm;



